@tailwind base;
@tailwind components;
@tailwind utilities;

/* html {
  height: 100%;
} */
body {
  /* height: 100%; */
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to bottom, rgba(23, 18, 50, 0.9), rgba(49, 22, 56, 0.9), rgba(53, 53, 119, 0.9));
}

/* This will let the app full-screen */
#root {
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
