/* Animation */

.floating-div {
  animation: floatUp 2s 1 ease-in-out; /* 使用floatUp動畫，2秒一次，無限循環 */
}

@keyframes floatUp {
  0% {
    transform: translateY(20px);
    opacity: 0.2;
  }
  50% {
    transform: translateY(0); /* 向上浮動的距離 */
    opacity: 0.7;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.centered-div {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.polygon-button {
  background-color: #EFD384;
  clip-path: polygon(0% 50%, 20% 20%, 80% 20%, 100% 50%, 80% 80%, 20% 80%);
  cursor: pointer;
}

.polygon-inner-button {
  background-color: #313150;
  clip-path: polygon(2% 50%, 21% 22%, 79% 22%, 98% 50%, 79% 78%, 21% 78%);
  color: #F8D781;
  cursor: pointer;
}

.polygon-inner-border {
  background-color: rgba(239, 211, 132, 0.5);
  clip-path: polygon(5% 50%, 22% 25%, 78% 25%, 95% 50%, 78% 75%, 22% 75%);
  color: #F8D781;
  cursor: pointer;
}

.polygon-p-section {
  background-color: #313150;
  clip-path: polygon(6% 50%, 22% 26%, 78% 26%, 94% 50%, 78% 74%, 22% 74%);
  color: #F8D781;
  font-size: 16px;
  cursor: pointer;
}

